import Vue from 'vue'
import Router from 'vue-router'
import store from '../vuex/store'
// import Hello from '@/components/Hello'
// import Index from '@/components/index'
import Lists from '@/components/lists'
import ListInfo from '@/components/listinfo'
import Home from '@/components/home'
import Login from '@/components/log'
import Edit from '@/components/editinfo'
Vue.use(Router)

var router  =  new Router({
	mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Lists',
      component: Lists,
      beforeEnter: (to,from,next)  => {
        // if(from.path == '/' || store.state.bl == false){
        //   store.state.userinfo = ''
        //   sessionStorage.setItem('user','')
        //   next(true)
        // }
        next(true)
      }

    },
    {
			path: '/login',
			name: 'Login',
			component: Login	
    },
    {
      path: '/user/:_id',
      name: 'Home',
      component: Home
    },
    {
      path: '/user/:_id/edit',
      name: 'Edit',
      component: Edit
    },
    {
			path: '/lists/:_listid',
			name: 'ListInfo',
			component: ListInfo	
    }
    

  ]
})
router.beforeEach((to, from,next) => {
  if(store.state.userinfo != sessionStorage.getItem('user')){
    axios.post('/api/init',{user:sessionStorage.getItem('user')}).then( res => {
      store.state.userinfo = sessionStorage.getItem('user')
      store.state.txts = JSON.parse(sessionStorage.getItem('txts'))
      next(true)
    }).catch( res => {
      next(true)
    })
  }else{
    next(true)
  }
})

export default router



