<template>
	<div class="edituser col-xs-8 col-lg-6 col-xs-offset-2 col-lg-offset-3">
		<form action="/api/adduserinfo" method="post" accept-charset="utf-8" enctype="multipart/form-data">
			<input type="text" v-model='path' v-show='false' name="path" value="" placeholder="">
			<div class="form-group">
		    <label class="text-info" for="name">Name</label>
		    <input type="text" name="name" v-model='name' class="form-control" id="name" >
		  </div>
		  <div class="form-group">
		    <label class="text-info" style="" for="state">State</label>
				<textarea name="state" rows="10" v-model='state' class="form-control" id="state" placeholder="state"></textarea>
		  </div>
		</form>
		<button type="" @click='submit' class="btn btn-info">updata</button>
	</div>		
</template>

<script>
import {mapActions} from 'vuex'
export default {
	name:'edituser',
	created () {
		this.path = this.$route.params
	},
	data () {
		return {
			path:'',
			name:'',
			state:'',
			files:''
		}
	},
	methods: {
		...mapActions(['cytxtdata']),
		submit () {
			if(this.name && this.state){
				axios.post('/api/updatauserinfo',{name:this.name,state:this.state,id:this.$route.params._id}).then( res=>{
						this.$store.dispatch('cytxtdata',res.data)
						this.$router.push('/user/'+this.$route.params._id)
				}).catch(res => {

				})
			}
			// var myform =  new FormData(document.querySelector('form'))
			// myform.append('user',this.$route.params)
			// console.log(myform.append)
			// var oReq = new XMLHttpRequest()
			// oReq.open('post','http://localhost:8080/api/adduserinfo')
			// oReq.onload = () => {
			// 	if(oReq.status == 200){
			// 		// this.$router.push(oReq.response)
			// 		console.log(oReq.response)
			// 	}else{
			// 		alert('find a err')
			// 	}
			// }

			// oReq.send(myform)
		}
	}

}	
</script>


