<template>
	<div id="listinfo" class="col-xs-8 col-lg-6 col-xs-offset-2 col-lg-offset-3 bor_sha" @click.prevent ='vby = false'>
		<h2>{{title}}</h2>
		<p>{{txt}}</p>
		<router-link :to="'/user/'+user">Doctor: {{doctoe}}</router-link>
		<div v-if='showUsers && showUsers == user '> 
			<textarea v-if='vby' name="" cols='100%' rows='10' v-model='msg' @click.stop=''></textarea>
			<button type="" class="btn btn-info" @click.stop='changeVby'>{{showText}}</button>
			<button type="" class="btn btn-danger" @click.stop='detext'>删除</button>
		</div>
	</div>
</template>
<script>
import { mapGetters , mapActions }	from 'vuex'
export default {
	name: 'listinfo',
	data () {
		return {
			texts:['编辑','保存'],
			text:'编辑',
			vby:false,
			user:'',
			title:'',
			doctoe:'',
			msg: '',
			txt: ''
		}
	},
	created  () {
		for(let i in this.showTxts){
				if(this.showTxts[i]._id == this.$route.params._listid){
					this.title = this.showTxts[i].title
					this.txt = this.msg =  this.showTxts[i].text
					this.doctoe = this.showTxts[i].fate.name || this.showTxts[i].fate.username
					this.user = this.showTxts[i].fate._id 
					return
				}
		}

	},
	computed: {
		...mapGetters([
			'showUsers',
			'showTxts'
		]),
		showText () {
			return this.text = this.texts[this.vby == 0?0:1]
		}
	},
	methods: {
		...mapActions([
			'cyuserdata'
		]),
		changeVby () {
			if(this.vby && this.txt != this.msg){
				this.txt = this.msg
				axios.post('/api/save',{title:this.title,text:this.txt,user:this.user}).then(res => {
					this.$store.dispatch('cytxtdata',res.data)
					sessionStorage.setItem('txts', JSON.stringify(res.data))
					alert('success')
				}).catch(res => {
					alert('err')
				})
			} 
			this.vby = !this.vby	
		},
		detext () {
			axios.post('/api/delate',{title:this.title,uid:this.showUsers,tid:this.$route.params._listid}).then(res => {
					this.$store.dispatch('cytxtdata',res.data)
					sessionStorage.setItem('txts', JSON.stringify(res.data))
					this.$router.push('/user/'+this.user)
				alert('success')
			}).catch(res => {
				alert('err')
			})
		}
	}
}
</script>

<style scoped>
#listinfo > h2 {
	color: #030303;
}	
#listinfo > p {
	color: #666;
}
#listinfo > span {
	color: #0080FF;
}
#listinfo textarea {
	resize:none;
	width: 100%;
	color: #000;
}
</style>



