<template>
	<div id="log" class="col-xs-8 col-sm-6 col-lg-4 col-xs-offset-2  col-sm-offset-3 col-lg-offset-4">
		<!-- <form> -->
		  <div class="form-group">
		  	<p class="msg">{{msg}}</p>
		    <label for="exampleInputEmail1">Username:En add Number</label>
		    <input type="text" class="form-control" id="exampleInputEmail1" placeholder="Username" v-model='username' onkeyup="value=value.replace(/[\W]/g,'') " onbeforepaste="clipboardData.setData('text',clipboardData.getData('text').replace(/[^\d]/g,''))">
		  </div>
		  <div class="form-group">
		    <label for="exampleInputPassword1">Password:En add Number</label>
		    <input type="password" class="form-control" id="exampleInputPassword1" placeholder="Password" v-model='password' onkeyup="value=value.replace(/[\W]/g,'') " >
		  </div>
		  <button class="btn btn-default" @click='login'>login</button>
		  <button class="btn btn-default" @click='sign '>sign</button>
		 <!--  <p>{{showUsers.username}}</p> -->
		<!-- </form> -->
	</div>
</template>

<script>

import { mapGetters,mapActions,mapMutations} from 'vuex'
import Router from 'vue-router'
export default {
	name:'log',
	data () {
		return {
			msg: '',
			username:'222',
			password:'222'
		}
	},
	computed: {
		...mapGetters([
			'showUsers'
		])
	},
	methods: {
		/*登入 enter*/
		...mapMutations([
			'userdata'
		]),
		...mapActions([
			'cyuserdata',
			'bl'
		]),
		login () {
			if(!this.username || !this.password) return
			axios.post('/api/login',{username:this.username,password:this.password}).then(res => {
				if(res.data){
					this.msg = '登入成功'
					let id = res.data._id;
					this.$store.dispatch('cyuserdata',id)
					sessionStorage.setItem('user',id)
					this.$store.dispatch('bl',true)
					this.$router.push('/user/'+id)
				}else{
					this.msg ='用户名或密码错误'
				}
			}).catch(res => {
				this.msg ='出错了'
			})
		},
		sign  () {
			if(!this.username || !this.password) return
			axios.post('/api/sign',{username:this.username,password:this.password}).then(res => {
				console.log(res.data)
				if(res.data == 'OK'){
					this.msg = "注册成功"
				}else{
					this.msg ='用户名已存在'
				}
			}).catch(res => {
					this.msg ='出错了'
			})
		}
		
	}
}	
</script>

<style scoped>
#log {
	border-radius: 5px;
	box-shadow: 1px 1px 5px rgba(0,0,0,.5);
	overflow: hidden;
	padding: 12px 24px;
	box-sizing: border-box;
	margin-top: 20px;
	z-index: 1;
}
#log label {
	color : #fff;
}
#log .msg {
	color: green;
}
#log:before {
	content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url('../assets/bj.jpg');
  z-index: -1;
  -webkit-filter: blur(20px);
  -moz-filter: blur(20px);
  -ms-filter: blur(20px);
  -o-filter: blur(20px);
  filter: blur(20px);
  background-position: center top;
	background-size: cover;
	background-attachment: fixed;
	margin: -30px;
}
</style>



