<template>
  <div id="app">
  	<nav class="nav navbar navbar-default ">
			<div class="navbar-header">
	      <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
	        <span class="sr-only">Toggle navigation</span>
	        <span class="icon-bar"></span>
	        <span class="icon-bar"></span>
	        <span class="icon-bar"></span>
	      </button>
	      <router-link class="navbar-brand" to="/">Index</router-link>
			</div>
			<div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
	      <ul class="nav navbar-nav">
	        <li v-if='!showUsers'><router-link to="/login">Login</router-link></li>
	        <li v-else><router-link style='display: inline-block;position: static;' :to="'/user/'+showUsers">userinfo</router-link><router-link style='display: inline-block;position: static;' to='/' @click.native='logout'>logout</router-link></li>
	      </ul>
    	</div><!-- /.navbar-collapse -->
	  </nav>
    <router-view></router-view>
  </div>
</template>
<script>
import { mapGetters,mapActions } from 'vuex'
export default {
  name: 'app',
  computed: {
		...mapGetters([
			'showUsers'
		])
		
	},
	methods: {
		...mapActions([
			'bl',
			'cyuserdata'
		]),
		logout () {
				axios.post('/api/logout').then( res => {
					this.$store.dispatch('cyuserdata','')
					sessionStorage.setItem('user', '')
				}).catch( res => {

				})
		}
	}
}
</script>

<style>
* {
	list-style: none;
}
html,body {
  height:100%;
}
a:hover {
	text-decoration: none;
}
textarea{
	resize:none
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height:100%;
  background-position: center top;
  background-size: cover;
  -webkit-background-size: cover;
  background-size: 100% 100%;
	background-image:url(./assets/bj.jpg);
	color: #fff;
}
.logout {
	display: inline-block;
}
.bor_sha {
	z-index: 1;
	padding: 12px;
	border-radius: 5px;
	box-shadow: 1px 1px 1px rgba(0,0,0,.3);
	position: relative;
	overflow: hidden;
}
.bor_sha:before {
	content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url('./assets/bj.jpg');
  z-index: -1;
  -webkit-filter: blur(20px);
  -moz-filter: blur(20px);
  -ms-filter: blur(20px);
  -o-filter: blur(20px);
  filter: blur(20px);
  background-position: center top;
	background-size: cover;
	background-attachment: fixed;
	margin: -30px;
}
/*a > button {
	border:none;
	background-color:transparent;
}*/
</style>



