<template>
	<div id="lists" class="col-xs-8 col-lg-6 col-xs-offset-2 col-lg-offset-3">
		<p v-show='bls'>最新列表前10:</p>
		<ul style="	top:30px;">
			<li class="box" v-for='list of showTxts'>
				<h2>
					<router-link class="text-warning fontSize" :to="'/lists/'+list._id">{{list.title}}</router-link>
				</h2>
				<p>{{list.text}}</p>
				<span>Doctor: {{ list.fate.name || list.fate.username }}</span>
			</li>
		</ul>
	</div>
</template>

<script>
import { mapActions,mapGetters } from 'vuex'
export default {
	name: 'lists',
	data () {
		return {
			now:0,
			bls:true,
			lists:[]
		}
	},
	created () {
		this.gettxt()
	},
	mounted () {
		this.shubiao()
	},
	computed : {
		...mapGetters([
			'showTxts'
		]),
		getlists () {
			if(this.lists!== null){
				return
			}else{
				return this.lists = this.showTxts.slice(0,10)
			}	
		}
	},
	methods: {
		...mapActions([
			'cytxtdata'
		]),
		gettxt () {
			axios.post('/api/getlists').then(res => {
				this.$store.dispatch('cytxtdata',res.data)
				sessionStorage.setItem('txts', JSON.stringify(res.data)) 
				
			}).catch(res => {
			})
		},
		shubiao () {
			if(document.addEventListener){
			  document.querySelector('#lists > ul').addEventListener('DOMMouseScroll',this.scrollFunc)
			}
			document.querySelector('#lists > ul').onmousewheel=this.scrollFunc;
		},
		scrollFunc (e) {
			e = e || window.event
			this.bls = false
			if(e.wheelDelta){
				this.now = e.wheelDelta > 0?10:-10
		    document.querySelector('#lists > ul').style.top =Number.parseInt(document.querySelector('#lists > ul').style.top)+this.now+'px'
		    if(Number.parseInt(document.querySelector('#lists > ul').style.top)>20){
		    	document.querySelector('#lists > ul').style.top = 30+'px'
		    	this.bls = true
		    }
	    }else if(e.detail){
	    	this.now = e.wheelDelta > 0?10:-10
       	document.querySelector('#lists > ul').style.top += this.now+'px'
	    }
		}
	}
}
</script>

<style scoped>
#lists {
	overflow: hidden;
	height: 80%;
	position: relative;
}
#lists > ul {
	width: 100%;
	position: absolute;
	padding-left: 0;
	box-sizing: border-box;
}
.box {
	padding:3px 10px 6px 10px;
	border-radius: 5px;
	box-shadow: 1px 1px 1px rgba(0,0,0,.3);
	background-color:rgba(0,0,0,.3);
	margin-bottom: 10px;
	box-sizing: border-box;
	width: 100%;
}	
.box > p {
	max-height: 60px;
	text-indent: 24px;
	overflow : hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 3;
	-webkit-box-orient: vertical;
}
</style>



