import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


var state = {
	bl:true,
	userinfo: '',
	txts:[]
}

const mutations = {
	userdata (state,data) {
		state.userinfo = data
	},
	txtdata (state,data) {
		state.txts = data
	},
	bl (state,data){
		state.bl = data
	}
}

const actions = {
	cyuserdata:({commit},data) => commit('userdata',data),
	cytxtdata:({commit},data) => commit('txtdata',data),
	bl:({commit},data) => commit('bl',data)
}

const getters = {
	showUsers(state){
		return state.userinfo
	},
	showTxts(state){
		return state.txts
	},
	bl(state){
		return state.bl
	}
}

export default new Vuex.Store({
	state,
	mutations,
	actions,
	getters
})


