<template>
	<div id="addtext">	
		<button type="button" class="btn btn-primary btn-lg" data-toggle="modal" data-target="#myModal">
		  Add New Document
		</button>

		<div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
		  <div class="modal-dialog" role="document">
		    <div class="modal-content">
		    	<span class='showmsg'>{{msg}}</span>
		      <div class="modal-header">
		        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
		      </div>
		      <div class="modal-body">
		        <div class="form-group">
					    <label class="text-info" for="utitle">Title</label>
					    <input type="text" v-model='title' class="form-control" id="utitle" placeholder="Title">
					  </div>
					  <div class="form-group">
					    <label class="text-info" style="" for="utext">Text</label>
					    <textarea name="" rows="16" v-model='text' class="form-control" id="utext" placeholder="Text"></textarea>
					  </div>
		      </div>
		      <div class="modal-footer">
		        <button type="button" class="btn btn-default" data-dismiss="modal">Close</button>
		        <button type="button" class="btn btn-primary save" @click='save'>Save changes</button>
		      </div>
		    </div>
		  </div>
		</div>
	</div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
	export default {
		name:'addtext',
		data () {
			return {
				title:'',
				text:'',
				msg:''
			}
		},
		computed: {
			...mapGetters(['showUsers'])
		},
		methods: {
			...mapActions(['cyuserdata']),
			save () {
				var el = document.querySelector('.save')
				var title = this.title
				var text = this.text
				if(title && text){
					axios.post('/api/textadd',{title:title,text:text,fate:this.showUsers}).then(res => {
						this.msg = 'success'
						el.setAttribute('data-dismiss', "modal" )
						document.querySelector('.modal-backdrop').style.display = 'none'
						this.title = this.text = ''
						this.$store.dispatch('cytxtdata',res.data[1])
						sessionStorage.setItem('txts', JSON.stringify(res.data[1]))
						this.$router.push('/lists/'+res.data[0])
					}).catch(res => {
						this.msg = 'error'
					})
				}else{
					this.msg = 'title or text not null'
				}
			},
			changeEl (fn) {

			}
		}
	}
</script>

<style scoped>
span.showmsg {
	position: absolute;
	width: 80%;
	left: 50%;
	margin: 10px 0 0 -40%;
	color:#0080FF ;
	text-align: center;
}	
</style>


