<template>
	<div id="home" class="col-lg-8 col-lg-offset-2">
		<div class="userdate bor_sha">
			<img src="../assets/bj.jpg" class="img-responsive" alt="Responsive image" style="width: 100px;height: 100px">
			<div class="editinfo">
				<p>{{name || username}}</p>
				<span style="font-size: 12px">{{state || '这个用户很懒，什么都没写'}}</span>
				<router-link v-if='showUsers == this.$route.params._id' class='pull-right' :to="'/user/'+this.$route.params._id+'/edit'">Edit</router-link>
			</div>
		</div>
		<em v-if='!texts'>没有文章</em>
		<ul v-else>
			<li v-for='item of texts' class="bor_sha">
				<router-link :to="'/lists/'+item._id">
					<span>{{item.title}}</span>
					<span>{{item.text}}</span>
					<span></span>
				</router-link>
			</li>
		</ul>
		<addtext v-if='showUsers == this.$route.params._id'></addtext>
		<router-view></router-view>
	</div>
</template>

<script>
import addtext from './addtext'
import {mapGetters} from 'vuex'
export default {
	name: 'home',
	data () {
		return {
			username:'',
			state:'',
			name:'',
			texts:''
		}
	},
	created () {
		axios.post('/api/getuser',{user:this.$route.params._id}).then( res => {
			let data = res.data
			this.username = data.username;
			this.name = data.name || '';
			this.state = data.state;
			this.texts = data.texts
		}).catch( res => {

		})
	},
	computed: {
		...mapGetters([
			'showUsers'
		])
	},
	components: {
		addtext
	}
}	
</script>

<style scoped>
.userdate > * {
	display: inline-block;
	vertical-align: bottom;
	font-size: 24px;
	color: #666;
	margin-right: 20px;
}	
.editinfo {
	min-width: 60%;
	max-width: 70%;
}
#home > ul {
	display: flex;
	flex-wrap:wrap ;
	margin-top:20px; 
	-webkit-padding-start:0;
}
#home > ul > li {
	flex-grow: 1;
	max-width:23%;
	min-width:23%;
	box-sizing: border-box;
	margin: 1%;
}
#home > ul > li  *  {
	display: block;
	overflow: hidden;
	text-overflow:ellipsis;
	white-space: nowrap;
}
</style>



